import { useState } from 'react';
import { motion } from 'framer-motion';
import { Calendar, Music, Users, Star, Mail, Phone, Pen } from 'lucide-react';
import EventSuccess from '../components/EventSuccess';
import { generateEventQRCode } from '../lib/qrcode';

interface EventData {
  eventId: number;
  eventTitle: string;
  date: string;
  time: string;
  name: string;
  email: string;
  phone: string;
  guests: number;
  qrCode?: string;
}

interface EventFormData {
  name: string;
  email: string;
  phone: string;
  guests: number;
  eventTitle: string;
  date: string;
  time: string;
  eventId?: number;
}

function Events() {
  const events = [
    {
      id: 1,
      title: "Flavour Tasting Night",
      date: "January 15, 2025",
      time: "7 PM - 10 PM",
      image: "/images/events/cachimberos-o7A76QuFT00-unsplash.jpg",
      description: "Join us for an exclusive tasting of our newest premium flavours."
    },
    {
      id: 2,
      title: "DJ Night",
      date: "January 20, 2025",
      time: "8 PM - 2 AM",
      image: "/images/events/DJ Night.jpg",
      description: "Experience the perfect blend of music and atmosphere with our resident DJ."
    },
    {
      id: 3,
      title: "Student Night",
      date: "January 25, 2025",
      time: "6 PM - 11 PM",
      image: "/images/events/Student Night.avif",
      description: "Special discounts and offers for students. Valid student ID required."
    }
  ];

  const [isSuccessModalOpen, setIsSuccessModalOpen] = useState(false);
  const [formData, setFormData] = useState<EventFormData>({
    name: localStorage.getItem('eventName') || '',
    email: localStorage.getItem('eventEmail') || '',
    phone: localStorage.getItem('eventPhone') || '',
    guests: 0,
    eventTitle: '',
    date: '',
    time: '',
  });
  const [validationError, setValidationError] = useState<string>('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [eventData, setEventData] = useState<EventData | null>(null);

  const handleEventSelect = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const selectedEventTitle = e.target.value;
    const selectedEvent = events.find(event => event.title === selectedEventTitle);
    
    if (selectedEvent) {
      setFormData(prev => ({
        ...prev,
        eventId: selectedEvent.id,
        eventTitle: selectedEvent.title,
        date: selectedEvent.date,
        time: selectedEvent.time,
        guests: prev.guests || 0
      }));
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    setValidationError('');

    try {
      const selectedEvent = events.find(event => event.title === formData.eventTitle);
      if (!selectedEvent || typeof selectedEvent.id !== 'number') {
        throw new Error('Selected event not found or invalid id');
      }

      const qrCodeDataURL = await generateEventQRCode({
        ...formData,
        eventId: Number(selectedEvent.id).toString(), // Convert eventId to string
        eventTitle: selectedEvent.title
      });
      
      // Store the event registration data with QR code
      const eventWithQR: EventData = {
        ...formData,
        eventId: Number(selectedEvent.id),
        eventTitle: selectedEvent.title,
        date: selectedEvent.date,
        time: selectedEvent.time.toString(),
        qrCode: qrCodeDataURL
      };

      // Show success modal with the data
      setEventData(eventWithQR);
      setIsSuccessModalOpen(true);
      
      // Clear form
      setFormData({
        name: '',
        email: '',
        phone: '',
        guests: 1,
        eventTitle: '',
        date: '',
        time: '',
      });
      
    } catch (error) {
      console.error('Event registration error:', error);
      setValidationError('Failed to register for event. Please try again.');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="min-h-screen pt-24 pb-8 bg-[#020B18]">
      <div className="container mx-auto px-4 max-w-5xl">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="text-center mb-6"
        >
          <h1 className="text-3xl md:text-4xl font-bold mb-2 bg-clip-text text-transparent bg-gradient-to-r from-primary-300 to-accent-400">
            Upcoming Events
          </h1>
          <p className="text-gray-400 text-sm md:text-lg">
            Join us for special events and unforgettable experiences. Contact us directly to register for events.
          </p>
          <p className="text-gray-400 text-sm md:text-lg mt-2">
            Call us at <a href="tel:01792555888" className="text-primary-300 hover:text-primary-400 transition-colors">01792 555888</a> or email <a href="mailto:info@sapphirelounge.com" className="text-primary-300 hover:text-primary-400 transition-colors">info@sapphirelounge.com</a>
          </p>
        </motion.div>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-12">
          {events.map((event, index) => (
            <motion.article
              key={event.id}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: index * 0.2 }}
              whileHover={{ y: -10 }}
              className="bg-dark-500/50 backdrop-blur-sm rounded-xl overflow-hidden border border-accent-700/20 shadow-xl"
            >
              <div className="relative h-56 md:h-72 overflow-hidden">
                <motion.img
                  whileHover={{ scale: 1.05 }}
                  transition={{ duration: 0.4 }}
                  src={event.image}
                  alt={`${event.title} at Sapphire Lounge`}
                  className="w-full h-full object-cover"
                />
                <div className="absolute inset-0 bg-gradient-to-t from-dark-500 via-dark-500/20 to-transparent" />
                <div className="absolute bottom-0 left-0 right-0 p-4 pb-2">
                  <h3 className="text-lg md:text-xl font-bold text-white mb-1">{event.title}</h3>
                  <p className="text-gray-200 text-xs md:text-sm mb-2">{event.description}</p>
                  <div className="flex items-center text-primary-300">
                    <Calendar className="w-4 h-4 mr-2" />
                    <span className="text-sm font-medium">{event.date} â€¢ {event.time}</span>
                  </div>
                </div>
              </div>
            </motion.article>
          ))}
        </div>

        {/* Feature Containers */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.6 }}
          className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-12"
        >
          <div className="p-4 md:p-6 bg-dark-500/50 backdrop-blur-sm rounded-xl border border-accent-700/20 shadow-xl">
            <Music className="w-6 h-6 md:w-8 md:h-8 text-primary-300 mb-3 md:mb-4" />
            <h3 className="text-lg md:text-xl font-semibold mb-2">Live Entertainment</h3>
            <p className="text-gray-300 text-sm">Weekly DJ performances and live music sessions</p>
          </div>

          <div className="p-4 md:p-6 bg-dark-500/50 backdrop-blur-sm rounded-xl border border-accent-700/20 shadow-xl">
            <Users className="w-6 h-6 md:w-8 md:h-8 text-primary-300 mb-3 md:mb-4" />
            <h3 className="text-lg md:text-xl font-semibold mb-2">Private Events</h3>
            <p className="text-gray-300 text-sm">Book our exclusive space for your special occasions</p>
          </div>

          <div className="p-4 md:p-6 bg-dark-500/50 backdrop-blur-sm rounded-xl border border-accent-700/20 shadow-xl">
            <Star className="w-6 h-6 md:w-8 md:h-8 text-primary-300 mb-3 md:mb-4" />
            <h3 className="text-lg md:text-xl font-semibold mb-2">VIP Experience</h3>
            <p className="text-gray-300 text-sm">Enjoy premium service and exclusive benefits</p>
          </div>
        </motion.div>

        {/* Event Registration Form */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.8 }}
          className="max-w-3xl mx-auto"
        >
          <div className="space-y-6 bg-black/40 backdrop-blur-sm rounded-xl p-4 border border-white/10 transition-colors hover:bg-black/50">
            <h2 className="text-2xl md:text-3xl font-bold mb-6 bg-clip-text text-transparent bg-gradient-to-r from-primary-300 to-accent-400">
              Event Registration
            </h2>
            
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="mb-6">
                <label htmlFor="event-title" className="block text-base md:text-lg font-medium text-gray-300 mb-2">
                  Select Event *
                </label>
                <div className="relative">
                  <Calendar className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5 pointer-events-none" />
                  <select
                    id="event-title"
                    name="event-title"
                    value={formData.eventTitle}
                    onChange={handleEventSelect}
                    className="w-full pl-10 pr-4 py-3 bg-neutral-800 border border-neutral-700 rounded-lg focus:ring-2 focus:ring-primary-400 focus:border-transparent"
                    required
                  >
                    <option value="">Select an event</option>
                    {events.map((event) => (
                      <option key={event.id} value={event.title}>
                        {event.title} - {event.date}
                      </option>
                    ))}
                  </select>
                </div>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <label htmlFor="event-name" className="block text-base md:text-lg font-medium text-gray-300 mb-2">
                    Name *
                  </label>
                  <div className="relative">
                    <Pen className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5 pointer-events-none" />
                    <input
                      type="text"
                      id="event-name"
                      name="event-name"
                      value={formData.name}
                      onChange={(e) => {
                        const newName = e.target.value;
                        setFormData({ ...formData, name: newName });
                        localStorage.setItem('eventName', newName);
                      }}
                      className="w-full pl-10 pr-4 py-3 bg-neutral-800 border border-neutral-700 rounded-lg focus:ring-2 focus:ring-primary-400 focus:border-transparent"
                      placeholder="Your name"
                      required
                    />
                  </div>
                </div>

                <div>
                  <label htmlFor="event-email" className="block text-base md:text-lg font-medium text-gray-300 mb-2">
                    Email *
                  </label>
                  <div className="relative">
                    <Mail className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5 pointer-events-none" />
                    <input
                      type="email"
                      id="event-email"
                      name="event-email"
                      value={formData.email}
                      onChange={(e) => {
                        const newEmail = e.target.value;
                        setFormData({ ...formData, email: newEmail });
                        localStorage.setItem('eventEmail', newEmail);
                      }}
                      className="w-full pl-11 pr-4 py-3 bg-neutral-800 border border-neutral-700 rounded-lg focus:ring-2 focus:ring-primary-400 focus:border-transparent"
                      placeholder="Your email"
                      required
                    />
                  </div>
                </div>

                <div>
                  <label htmlFor="event-phone" className="block text-base md:text-lg font-medium text-gray-300 mb-2">
                    Phone *
                  </label>
                  <div className="relative">
                    <Phone className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5 pointer-events-none" />
                    <input
                      type="tel"
                      id="event-phone"
                      name="event-phone"
                      value={formData.phone}
                      onChange={(e) => {
                        const newPhone = e.target.value;
                        setFormData({ ...formData, phone: newPhone });
                        localStorage.setItem('eventPhone', newPhone);
                      }}
                      className="w-full pl-11 pr-4 py-3 bg-neutral-800 border border-neutral-700 rounded-lg focus:ring-2 focus:ring-primary-400 focus:border-transparent"
                      placeholder="Your phone number"
                      required
                    />
                  </div>
                </div>

                <div>
                  <label htmlFor="guests" className="block text-base md:text-lg font-medium text-gray-300 mb-2">
                    Number of Guests *
                  </label>
                  <div className="relative">
                    <Users className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5 pointer-events-none" />
                    <select
                      id="guests"
                      value={formData.guests || ''}
                      onChange={(e) => setFormData(prev => ({ 
                        ...prev, 
                        guests: parseInt(e.target.value) 
                      }))}
                      className="w-full pl-11 pr-4 py-3 bg-neutral-800 border border-neutral-700 rounded-lg focus:ring-2 focus:ring-primary-400 focus:border-transparent"
                      required
                      autoComplete="off"
                    >
                      <option value="">Select number of guests</option>
                      {[...Array(8)].map((_, i) => (
                        <option key={i + 1} value={i + 1}>
                          {i + 1} {i === 0 ? 'Guest' : 'Guests'}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
              </div>

              {validationError && (
                <div className="text-red-400 text-sm mt-2 bg-red-900/20 p-3 rounded-lg">
                  {validationError}
                </div>
              )}

              <button
                type="submit"
                disabled={isSubmitting}
                className="w-full py-3 px-4 bg-gradient-to-r from-primary-400 to-accent-400 text-white rounded-lg font-medium
                  hover:from-primary-500 hover:to-accent-500 focus:outline-none focus:ring-2 focus:ring-primary-300 
                  transition-all duration-300 transform hover:scale-[1.02]"
              >
                {isSubmitting ? 'Processing...' : 'Register for Event'}
              </button>
            </form>
          </div>
        </motion.div>

        {isSuccessModalOpen && (
          <EventSuccess
            isOpen={isSuccessModalOpen}
            onClose={() => {
              setIsSuccessModalOpen(false);
              setFormData({
                name: '',
                email: '',
                phone: '',
                guests: 1,
                eventTitle: '',
                date: '',
                time: '',
              });
            }}
            eventData={eventData || undefined}
          />
        )}
      </div>
    </div>
  );
}

export default Events;